import React, { useState } from 'react'
import styled from 'styled-components'
import { BiWinkSmile, BiPen } from 'react-icons/bi'
import { IoCodeWorking } from 'react-icons/io5'
import { useNavigate } from 'react-router-dom'
import _ from 'lodash'
import { blogLanguageMenu } from '../../store/staticData'

const MenuBox = () => {
  const navigate = useNavigate()
  const [openMenuSet, setOpenMenuSet] = useState({
    about: false,
    blog: false,
    project: false,
  })

  const returnMenu = (data) => {
    return _.map(data, (item) => (
      <h5 style={{ padding: 10 }} onClick={() => navigate(item.navi)}>
        - {item.menuName}
      </h5>
    ))
  }

  return (
    <OutWrap>
      <ClickMenuBox
        onClick={() => setOpenMenuSet({ project: false, blog: false, about: !openMenuSet.about })}>
        <BiWinkSmile
          size={20}
          style={{ marginRight: 10, color: openMenuSet.about ? 'green' : 'white' }}
        />
        <h3 style={{ color: openMenuSet.about ? 'green' : 'white' }}>About ME</h3>
      </ClickMenuBox>

      <div style={{ display: 'flex', flexDirection: 'column' }}>
        <ClickMenuBox
          style={{ display: 'flex' }}
          onClick={() => setOpenMenuSet({ about: false, project: false, blog: !openMenuSet.blog })}>
          <BiPen
            size={20}
            style={{ marginRight: 10 }}
            color={openMenuSet.blog ? 'green' : 'white'}
          />
          <h3 style={{ color: openMenuSet.blog ? 'green' : 'white' }}>BLOG</h3>
        </ClickMenuBox>
        {openMenuSet.blog && (
          <BlogMenuBox>
            <h4 style={{ marginTop: 5, marginBottom: 5 }}>Language</h4>
            {returnMenu(blogLanguageMenu)}
          </BlogMenuBox>
        )}
      </div>

      <ClickMenuBox
        style={{ display: 'flex' }}
        onClick={() =>
          setOpenMenuSet({ about: false, blog: false, project: !openMenuSet.project })
        }>
        <IoCodeWorking
          size={20}
          style={{ marginRight: 10 }}
          color={openMenuSet.project ? 'green' : 'white'}
        />
        <h3 style={{ color: openMenuSet.project ? 'green' : 'white' }}>PROJECT</h3>
      </ClickMenuBox>
    </OutWrap>
  )
}

export default MenuBox

const OutWrap = styled.div`
  color: white;
  padding: 20px 0 0 25px;
`

const ClickMenuBox = styled.div`
  display: flex;
  align-items: flex-end;
  cursor: pointer;
  margin-top: 20px;
  font-size: 18px;
`

const BlogMenuBox = styled.div`
  padding: 10px;
  margin-left: 20px;

  h5 {
    cursor: pointer;
    &:hover {
      color: green;
    }
  }
`
